	module.exports = {"data":{"markdownRemark":{"html":"<h2>Bug Bounties</h2>\n<p>Bug bounties are programs that are set up by companies to encourage people to check their products for vulnerabilities.  In return these companies offer cash prizes for the discovered vulnerabilities.</p>\n<h3>Benefits to bounty hunters</h3>\n<p>The benefits to the bounty hunters are fairly straightforward.  They get paid for what they find and get to improve their skills</p>\n<h3>Benefits to companies</h3>\n<p>The companies that sponsor these programs gain several benefits:</p>\n<ul>\n<li>Many eyes on their product are more likely to find more bugs than the typical QA team</li>\n<li>Only have to pay for results, not for the time spent trying to find bugs</li>\n<li>Encourages people who find vulnerabilties to turn them over to the company and not to the black market.</li>\n</ul>","fields":{"slug":"/security/bug-bounties/"},"frontmatter":{"title":"Bug Bounties"}}},"pathContext":{"slug":"/security/bug-bounties/"}}

